body {
  background: #f2f2f2; }

.hidden {
  opacity: 0; }

.App {
  min-height: calc(100vh - 80px);
  max-width: 1300px;
  width: calc(100% - 48px);
  margin: 0 auto;
  padding: 40px 24px;
  display: grid;
  grid-gap: 24px;
  grid-template-columns: 1fr 300px;
  grid-template-areas: "main sidebar" "footer footer"; }

.main {
  grid-area: main; }

.sidebar {
  grid-area: sidebar; }

@media (max-width: 860px) {
  .App {
    grid-template-columns: 1fr;
    grid-template-areas: "sidebar" "main" "footer"; } }

.toast {
  top: 40px !important; }
  .toast div {
    background: #4caf50;
    font-weight: bold;
    min-width: 40px;
    width: 51px; }

.header {
  padding: 40px 0;
  margin: 0px auto 16px;
  text-align: center;
  font-size: 1.6rem;
  background: #2979ff;
  border-radius: 4px;
  color: white;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12); }
  .header h1 {
    margin: 0 auto; }

.deleteToggle {
  width: 100%;
  display: flex;
  justify-content: flex-end; }

.copyblock {
  margin: 1.2rem auto;
  -webkit-transition: 300ms;
  transition: 300ms; }
  .copyblock div {
    padding-bottom: 16px !important; }
  .copyblock p {
    font-size: 1.6rem;
    color: #0000008a; }

.form {
  display: flex;
  flex-wrap: wrap; }

.form > * {
  flex: 1 1 100%; }

.form__button-container {
  display: flex;
  justify-content: flex-end;
  margin: 1.2rem auto; }
  .form__button-container button {
    flex: 0 1 120px;
    justify-self: flex-end; }

.form__textarea textarea {
  font-size: 1.4rem;
  line-height: 1.1; }

.footer {
  padding: 1.6rem 0;
  margin: 0 auto;
  text-align: center;
  grid-area: footer; }
  .footer a {
    color: #0000008a !important; }

